	module.exports = {"data":{"markdownRemark":{"html":"<p>ひらがなで書くとなんかみょんな愛着を感じるなぁ'Ａ｀</p>\n<h2>ＰＣもしくは本棚に入ってる『まりさ』</h2>\n<ul>\n<li>コピ本・オフセ本いっぱい(しかも過半数が魔理沙メイン)</li>\n<li>いつぞやの恋色音楽祭</li>\n<li>いつぞやの変色音楽祭</li>\n<li>とびまりさ</li>\n<li>ラマリサのポスター</li>\n<li>自作魔理沙帽(ハロウィン帽を改造したもの)</li>\n</ul>\n<h2>今妄想している『まりさ』(建前)</h2>\n<ul>\n<li>アリスほど露骨ではないが微妙にツンデレ</li>\n<li>平静時は攻めだが感情が高まると途端に受けになる</li>\n<li>アリスか霖之助か霊夢のうち誰か1人でも居なくなると1ヶ月もしないうちに泣き出しそう</li>\n<li>これが俺のマイジャスティスゥ！１！</li>\n</ul>\n<h2>今妄想している『まりさ』(本音)</h2>\n<ul>\n<li>魔 理 沙 の 尻 で ご は ん 七 百 杯 い け る</li>\n<li>……分かったからそこの箒、　俺　と　代　わ　れ</li>\n</ul>\n<p><strong>最初に出会った『まりさ』</strong> </p>\n<p>ひがし氏にC64の戦利品を押し付けられ、その中に妖々夢が入ってたのが東方との出会い…と同時に魔理沙との出会いでもあった。ちなみに当初は霊夢と比べて操作に癖があったり口調が突飛だったり、と東方の中でも余り好きなキャラではなかった。 今でも魔理沙の操作が苦手なのは相変わらずで所見プレイだけは必ず霊夢を使ってたり。'Ａ｀</p>\n<h2>特別な思い入れのある 『まりさ』</h2>\n<p>東方萃夢想の魔理沙 理由は あ え て 言 う ま い<br>\n恋色音楽祭のキャッチコピー 理由は あ え て 言 う ま い</p>\n<h2>あなたにとって『まりさ』とは</h2>\n<p>もし居なかったら時封城をラストにdanmaq解体して一介の名無しとして細々と合同企画とかに参加してる程度だった。そのくらいの存在。</p>","frontmatter":{"cover":null,"date":"2006-03-05T02:59:00+09:00","strDate":"2006/3/4","title":"『まりさ』"}}},"pathContext":{"path":"/ja/blog/2006/03/05-『まりさ』/","langKey":"ja"}}

